// @keyframes {{{
@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes caretFadeIn {
    0% {
        opacity: 0;
        visibility: hidden;
        transform: translateY(6px);
    }
    100% {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }
}
@keyframes fadeIn {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    100% {
        opacity: 1;
        visibility: visible;
    }
}
// }}}

// header {{{
header {
    padding: 1rem 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 999;
    transition: all .3s ease;
    animation: fadeIn .5s ease;
    animation-delay: .5s;
    animation-fill-mode: both;
    &.stuck {
        background: rgba($black, .9);
    }
    .grid-container {
        display: flex;
        align-items: center;
    }
    .grid-60 {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        .phone, .consult {
            position: relative;
            display:inline-block;
            padding: .5rem 1.5rem;
            &::after {
                content: '';
                background: $white;
                width: 1px;
                height: 60%;
                display: inline-block;
                margin: auto;
                top: 0;
                transform: skew(-20deg);
                bottom: 0;
                right: 0rem;
                position: absolute;
            }
            @include bp(sm) {
                display:none;
            }
        }
    }
    .hamburger {
        color: $white;
        display: flex;
        align-items: center;
        > span {
            &:first-of-type {
                margin-right: .4rem;
            }
        }
    }
    a {
        color: $white;
        text-transform: uppercase;
        font-family: $alt-stack1;
        &:hover {
            color: rgba($white, .7);
        }
    }
}
// }}}

// .close-overlay {{{
.close-overlay {
    position:fixed;
    background: rgba(0,0,0,.2);
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    z-index: 4;
    transition: all .3s ease;
    &.active {
        opacity: 1;
        visibility: visible;
    }
}
// }}}

// #banner {{{
#banner {
    background: url(../images/banner-main.png) no-repeat center;
    background-size: cover;
    // background-attachment: fixed;
    animation: fadeIn .5s ease;
    animation-delay: .5s;
    animation-fill-mode: both;
    .grid-100 {
        text-align: center;
        height: 760px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        padding-bottom: 8rem;
        h1 {
            color: $white;
            line-height: 1.1;
            font-family: $alt-stack1;
            font-weight: 700;
            margin-bottom: 2rem;
            font-size: 2.5rem;
            text-transform: uppercase;
            animation: fadeInUp .5s ease;
            animation-delay: 1s;
            animation-fill-mode: both;
            &:first-of-type {
                margin-bottom: 0;
            }
            @include bp(md) {
                display: inline-block;
                font-size: 3.5rem;
                &:first-of-type {
                    margin-right: 1rem;
                    margin-bottom: 2rem;
                }
            }
            @include bp(desktop) {
                font-size: 4.5rem;
            }
            @include bp(xl) {
                font-size: 5rem;
            }
        }
        .btn {
            animation: fadeInUp .5s ease;
            animation-delay: 1.25s;
            animation-fill-mode: both;
        }
        @include bp(md) {
            height: 1000px;
            padding-bottom: 12rem;
        }
        @include bp(xl) {
            height: 45.45vw;
            padding-bottom: 8.73vw;
        }
    }
}
// }}}

// #desktop-menu {{{
#desktop-menu {
    display: none;
    position: fixed;
    top: 0;
    // background-color: rgba(0,0,0,.9);
    background-color: $black;
    width: 100%;
    padding: 8rem 0 6rem;
    opacity: 0;
    visibility: hidden;
    transition: all .5s ease;
    > div {
        &:not(:first-child) {
            display: none;
        }
        &.active {
            display: block;
            > ul {
                animation: fadeIn 1s ease;
                // animation-delay: .1s;
                animation-fill-mode:both;
            }
        }
    }
    &.open {
        z-index: 10;
        opacity: 1;
        visibility: visible;
    }
    @include bp(desktop) {
        display: block;
    }
    ul {
        padding-bottom:0;
    }
    a {
        color: $white;
        display: inline-block;
        padding: .25rem 1rem;
        font-size: .875rem;
        @media only screen and (min-width: 1280px) {
            font-size: 1rem;
        }
        &:hover {
            color: $yellow;
        }
    }
    .grid-container {
        max-width: 100%;
        @media only screen and (min-width: 1280px) {
            padding: 0 30px;
        }
    }
    #menu-desktop-menu {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 2rem;
        @media only screen and (min-width: 1280px) {
            padding-right: 41px;
            padding-left: 41px;
        }
        li {
            flex: 1;
            padding: 0 .25rem;
            a {
                color: $white;
                text-align: center;
                display: block;
                // text-transform: uppercase;
                font-weight: 600;
                position: relative;
                padding: .75rem 0rem;
                &:hover {
                    color: $yellow;
                }
            };
            &.active {
                a {
                    // background: $yellow;
                    color: $yellow;
                    // padding-bottom: 1rem;
                    &::after {
                        content: '\f078';
                        font-family:'Font Awesome 5 Free';
                        font-weight:900;
                        display: block;
                        text-align: center;
                        left: 0;
                        right: 0;
                        bottom: -.5rem;
                        position: absolute;
                        animation: caretFadeIn .5s ease;
                        animation-fill-mode: both;
                    }
                }
            }
        }
    }
    #menu-procedures, #menu-medspa {
        .menu-item-has-children {
            > a {
                text-transform: uppercase;
                font-weight: 700;
                font-size: 1.125rem;
                margin-top: 2rem;
            }
        }
        img {
            width: 100%;
        }
    }
}
